import React, { Component } from "react";
class IfrContainer extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className="customize-iframe">
                <iframe
                    fieldid="customize_iframe"
                    id="customizeiframe"
                    src={this.props.ifr}
                    frameBorder="0"
                    scrolling="yes"
                />
            </div>
        );
    }
}
export default IfrContainer;
